
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import axios from "axios";
import { url } from "../common/global";
import { error } from "util";

export default {
name: "Login",
components: {
CheckIcon,
Toast
},
data() {
return {
username: "",
password: "",
checked: false,
showToast: false,
msg: ""
};
},
mounted() {
this.getCookie();
},
methods: {
login() {
axios({
method: "get",
url: "http://192.168.0.145:8080/login",

data: {},
headers: {
username: this.username,
password: this.password,
projectEnglishName: "recycledwater"
}
})
.then(res => {
debugger
console.log(JSON.stringify(res));
if (res.data.status == true) {
localStorage.setItem("username", this.username);
localStorage.setItem("token", res.data.token);
if(res.data.stationId){
localStorage.setItem("stationId", res.data.stationId);
localStorage.setItem("stationName", res.data.stationName);
}
if (this.checked == true) {
this.setCookie(this.username, this.password, 7);
} else {
this.clearCookie();
}
this.$router.push({ path: "/home" });
} else {
this.msg = "";
this.showToast = true;
}
})
.catch(error => {
console.log(JSON.stringify(error));
this.msg = "";
this.showToast = true;
});
},

setCookie(c_name, c_pwd, exdays) {
var exdate = new Date(); 
exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); 

window.document.cookie =
"userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
window.document.cookie =
"userPwd" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
window.document.cookie =
"checked" + "=" + this.checked + ";path=/;expires=" + exdate.toGMTString();
},

getCookie() {
if (document.cookie.length > 0) {
var arr = document.cookie.split("; "); 
for (var i = 0; i < arr.length; i++) {
var arr2 = arr[i].split("="); 

if (arr2[0] == "userName") {
this.username = arr2[1]; 
} else if (arr2[0] == "userPwd") {
this.password = arr2[1];
} else if (arr2[0] == "checked") {
this.checked = arr2[1];
}
}
}
},

clearCookie() {
this.setCookie("", "", -1); 
}
}
};



