import { childMixin } from "../../mixins/multi-items";

export default {
  name: "tabbar-item",
  mixins: [childMixin],
  props: {
    link: [String, Object]
  },
  computed: {
    isActive() {
      return this.$parent.index === this.currentIndex;
    },
    classes() {
      return {
        "current": this.$parent.index === this.currentIndex
      };
    }
  }
};



