
import Select from "./select/select";
import SelectItem from "./select/select-item";
import Datetime from 'vux/src/components/datetime/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
name: "DatePanel",
components: { Select, SelectItem, Datetime, Toast },
data() {
return {
showTime: false,
startTime: "",
endTime: "",
warnToast:false,
msg:''
};
},
watch: {
startTime() {
this.ifTime()
},
endTime() {
this.ifTime()
}
},
mounted(){
this.onSelect(24)
},
methods: {
onSelect(e) {
let endTime = new Date();
let startTime24 = new Date(endTime - 24 * 60 * 60 * 1000);
let startTime7 = new Date(endTime - 7 * 24 * 60 * 60 * 1000);
let startTime30 = new Date(endTime - 30 * 24 * 60 * 60 * 1000);
switch (e) {
case 24:
this.showTime = false;
this.$emit("selectDate", startTime24, endTime);
break;
case 7:
this.showTime = false;
this.$emit("selectDate", startTime7, endTime);
break;
case 30:
this.showTime = false;
this.$emit("selectDate", startTime30, endTime);
break;
case 0:
this.showTime = true;
}
},
startChange(e) {
this.startTime = e;
},
endChange(e) {
this.endTime = e;
},
ifTime(){
if (
this.endTime != "" &&
new Date(this.startTime) < new Date(this.endTime)
) {
if((new Date(this.endTime) - new Date(this.startTime))/( 1000 * 60 * 60 * 24)>90){
this.msg="90"
this.warnToast=true
}else{
this.$emit("selectDate", this.startTime + " 00:00:00", this.endTime + " 00:00:00");
}
}else if(
this.endTime != "" &&
new Date(this.startTime) > new Date(this.endTime)
){
this.msg=""
this.warnToast=true
}
}
}
};



