
import Cell from "@/components/cell";
import axios from "axios";
import Toast from 'vux/src/components/toast/index.vue'

import {url} from "../common/global";

export default {
name: "Real",
components: { Cell, Toast },
data() {
return {
dataList: [],
warnToast: false
};
},
mounted() {
this.getSensorsStationId();
},
methods: {
getSensorsStationId() {
const _vm = this;
axios({
method: "get",
url: "http://192.168.0.145:8080/sensors-stationId/" + localStorage.getItem("stationId"),

headers: {
username: localStorage.getItem("username"),
stationId: localStorage.getItem("stationId"),
token: localStorage.getItem("token"),
uri: "sensors-stationId",
type: "API"
}
})
.then(res => {
console.log(JSON.stringify(res))
if (res.data) {
var oldlist = res.data.data;
for (let i = 0; i < oldlist.length; i++) {
if (oldlist[i].unit == "") {
oldlist[i].unit = "";
}
}
this.getSensorData(oldlist, _vm);
}
})
.catch(error => {
console.log(JSON.stringify(error))

this.warnToast = true;
});
},

getSensorData(oldlist, _vm) {
let ws = null;
let list = oldlist;
if ("WebSocket" in window) {
ws = new WebSocket("ws://192.168.0.220:18888" + "//webSocket/123456");
ws.onopen = function() {
let id = [];
list.forEach(element => {
id.push(element.equipId);
});
ws.send(JSON.stringify({ type: "recyclewater", data: id }));
};
ws.onmessage = function(evt) {
let num = JSON.parse(evt.data);
for (let i = list.length - 1; i >= 0; i--) {
for (let j = num.length - 1; j >= 0; j--) {
if (list[i].equipId == num[j].id) {
list[i].num = num[j].value;
}
}
}
_vm.dataList = list;
};
}
}
}
};



