import echarts from "echarts";
import F2 from "@antv/f2";

export default {
  name: "line-chart",
  props: {
    items: Object,
    xid: Number
  },
  computed: {
    getxid() {
      return `xid${this.xid}`;
    }
  },
  mounted() {
    // this.drawChart();
    this.drawF2();
  },
  methods: {
    drawChart() {
      const option = {
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: this.items.x,
            axisLine: {
              lineStyle: {
                type: "solid",
                color: "#b5b5b5", //
                width: "1" //
              }
            },
            axisLabel: {
              textStyle: {
                color: "#777777" //
              }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: this.items.type,
            axisLine: {
              lineStyle: {
                type: "solid",
                color: "#b5b5b5", //
                width: "1" //
              }
            },
            axisLabel: {
              textStyle: {
                color: "#777777" //
              }
            }
          }
        ],
        series: {
          data: this.items.y,
          type: "line",
          itemStyle: {
            normal: {
              color: "#03bddc"
            }
          }
        }
      };
      const chartObj = echarts.init(document.getElementById(this.xid));
      chartObj.setOption(option);
      window.addEventListener("resize", function() {
        chartObj.resize();
      });
    },
    drawF2() {
      var chart = new F2.Chart({
        id: `xid${this.xid}`,
        pixelRatio: window.devicePixelRatio
      });
      chart.source(this.items.data, {
        value: {
          tickCount: 5,
          min: 0
        },
        date: {
          type: "timeCat",
          range: [0, 1],
          tickCount: 3
        }
      });
      chart.tooltip({
        showItemMarker: false,
        onShow: function onShow(ev) {
          var items = ev.items;
          items[0].name = items[0].title;
        }
      });
      // chart.axis("date", {
      //   label: function label(text, index, total) {
      //     var textCfg = {};
      //     if (index === 0) {
      //       textCfg.textAlign = "left";
      //     } else if (index === total - 1) {
      //       textCfg.textAlign = "right";
      //     }
      //     return textCfg;
      //   }
      // });
      chart.clear(); 
      // chart.repaint();
      chart.line().position("x*y");
      chart.render();
    }
  }
};



